export default class AppModel {
  constructor(state) {
    this.state = state;
    this.arr = [];
    this.url = 'https://www.googleapis.com/youtube/v3/videos?key=AIzaSyBePXyLkmQFVZ0IKpbmVzUpJ8X57aAaJ5g&id=';
    this.endUrl = '&part=snippet,statistics';
    this.listId = '';
    this.nextPageToken = '';
  }

  async getClipsInfo() {
    const response = await fetch(this.state);
    const data = await response.json();
    this.nextPageToken = data.nextPageToken;
    // eslint-disable-next-line no-plusplus
    for (let i = 0; i < data.items.length; i++) {
      this.arr = this.arr.concat(data.items[i].id.videoId);
    }
    this.listId = this.arr.join();
    const newResponse = await fetch(`${this.url}${this.listId}${this.endUrl}`);
    const newData = await newResponse.json();
    return newData.items;
  }
}
