import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      url: 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyBePXyLkmQFVZ0IKpbmVzUpJ8X57aAaJ5g&type=video&part=snippet&maxResults=15&q=',
      timer: null,
      slideIndex: 1,
      position: 0,
      Model: '',
      PageToken: '',
      counter: 0,
      elem: {},
    };
  }

  async start() {
    App.renderSearch();
    this.initEventListeners();
  }

  static renderSearch() {
    const searchbox = document.createElement('input');
    searchbox.id = 'searchbox';
    searchbox.type = 'text';
    searchbox.placeholder = 'Type here for search';
    document.body.appendChild(searchbox);
    const progress = document.createElement('div');
    progress.id = 'progress';
    progress.display = 'none';
    progress.className = 'blink';
    progress.innerHTML = 'Loading . . .';
    document.body.appendChild(progress);
  }

  initEventListeners() {
    document.getElementById('searchbox').addEventListener('keyup', () => {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => this.startSearch(), 3000);
      setTimeout(this.loading, 500);
      setTimeout(this.stopLoading, 2900);
    });
  }

  loading() {
    this.elem = document.getElementById('progress');
    this.elem.style.display = 'block';
  }

  stopLoading() {
    this.elem = document.getElementById('progress');
    this.elem.style.display = 'none';
  }

  async startSearch() {
    const request = document.getElementById('searchbox').value;
    this.Model = new AppModel(`${this.state.url}${request}`);
    const Data = await this.Model.getClipsInfo();
    this.PageToken = `&pageToken=${await this.Model.nextPageToken}`;
    this.state.counter = Data.length;
    const view = new AppView(Data);
    view.render();

    const nextPage = document.getElementById('nextPage');
    const currentPage = document.getElementById('currentPage');
    const previousPage = document.getElementById('previousPage');
    const firstPage = document.getElementById('firstPage');

    nextPage.addEventListener('click', () => {
      this.state.slideIndex += 1;
      currentPage.innerHTML = `${this.state.slideIndex}`;
      firstPage.className = firstPage.className.replace(' active', '');
      currentPage.className += ' active';
      currentPage.style.display = 'inline-block';
      previousPage.style.display = 'inline-block';
      this.state.position = this.state.position - document.getElementById('main').offsetWidth;
      document.getElementById('secondSection').style.marginLeft = `${this.state.position}px`;
      const mainWidth = document.getElementById('main').offsetWidth;
      const itemElem = document.getElementsByClassName('item').item(0);
      const itemWidth = itemElem.offsetWidth + Number(window.getComputedStyle(itemElem).marginRight.replace('px', ''));
      const viewItemCount = mainWidth / itemWidth;
      if (this.state.slideIndex * viewItemCount + viewItemCount >= this.state.counter) {
        this.moreResults();
      }
    });

    previousPage.addEventListener('click', () => {
      this.state.slideIndex -= 1;
      currentPage.innerHTML = `${this.state.slideIndex}`;
      if (this.state.slideIndex === 1) {
        previousPage.style.display = 'none';
        currentPage.style.display = 'none';
        currentPage.className = currentPage.className.replace(' active', '');
        firstPage.className += ' active';
      }
      this.state.position = this.state.position + document.getElementById('main').offsetWidth;
      document.getElementById('secondSection').style.marginLeft = `${this.state.position}px`;
    });

    firstPage.addEventListener('click', () => {
      this.state.slideIndex = 1;
      currentPage.innerHTML = `${this.state.slideIndex}`;
      if (this.state.slideIndex === 1) {
        previousPage.style.display = 'none';
        currentPage.style.display = 'none';
        currentPage.className = currentPage.className.replace(' active', '');
        firstPage.className += ' active';
      }
      this.state.position = 0;
      document.getElementById('secondSection').style.marginLeft = 0;
    });
  }

  async moreResults() {
    const request = document.getElementById('searchbox').value;
    this.Model = new AppModel(`${this.state.url}${request}${this.PageToken}`);
    const newData = await this.Model.getClipsInfo();
    this.PageToken = `&pageToken=${await this.Model.nextPageToken}`;
    this.state.counter += newData.length;
    const newView = new AppView(newData);
    newView.add();
  }
}
