export default class AppView {
  constructor(info) {
    this.info = info;
    this.section = {};
    this.newSection = {};
    this.newDiv = [];
  }

  renderMore() {
    this.section.innerHTML = this.info.map(item => `<div class="item">
    <img src="${item.snippet.thumbnails.medium.url}" width="300" height="180">
    <ul>
    <li class="title"><a href="https://www.youtube.com/watch?v=${item.id}" target="_blank">${item.snippet.title}</a></li>
    <li class="channelTitle">${item.snippet.channelTitle}</li>
    <li class="data">${item.snippet.publishedAt.substr(0, 10)}</li>
    <li class="views">${item.statistics.viewCount}</li>
    <li class="description">${item.snippet.description.substr(0, 100)}...</li>
    </ul>
    </div>`).join('');
  }

  render() {
    const content = document.createElement('main');
    content.id = 'main';
    if (document.getElementById('main')) {
      document.body.removeChild(document.getElementById('main'));
    }
    document.body.appendChild(content);
    const firstSection = document.createElement('section');
    firstSection.id = 'firstSection';
    firstSection.style.marginLeft = '0px';
    content.appendChild(firstSection);

    this.section = document.createElement('section');
    this.section.id = 'secondSection';
    this.section.style.marginLeft = '0px';
    firstSection.appendChild(this.section);

    this.renderMore();

    const div = document.createElement('div');
    div.className = 'slider-dots';
    content.appendChild(div);
    const firstPage = document.createElement('span');
    firstPage.id = 'firstPage';
    firstPage.className = 'slider-dots_item active';
    firstPage.innerHTML = '1';
    div.appendChild(firstPage);
    const previousPage = document.createElement('span');
    previousPage.id = 'previousPage';
    previousPage.className = 'slider-dots_item';
    previousPage.innerHTML = '⇦';
    div.appendChild(previousPage);
    const currentPage = document.createElement('span');
    currentPage.id = 'currentPage';
    currentPage.className = 'slider-dots_item';
    div.appendChild(currentPage);
    const nextPage = document.createElement('span');
    nextPage.id = 'nextPage';
    nextPage.className = 'slider-dots_item';
    nextPage.innerHTML = '⇨';
    div.appendChild(nextPage);
  }

  add() {
    this.newDiv = document.getElementById('secondSection');
    this.newSection = this.newDiv.cloneNode(true);
    this.newSection.innerHTML = this.info.map(item => `<div class="item">
    <img src="${item.snippet.thumbnails.medium.url}" width="300" height="180">
    <ul>
    <li class="title"><a href="https://www.youtube.com/watch?v=${item.id}" target="_blank">${item.snippet.title}</a></li>
    <li class="channelTitle">${item.snippet.channelTitle}</li>
    <li class="data">${item.snippet.publishedAt.substr(0, 10)}</li>
    <li class="views">${item.statistics.viewCount}</li>
    <li class="description">${item.snippet.description.substr(0, 100)}...</li>
    </ul></div>`).join('');
    this.newSection.style.marginLeft = 0;
    document.getElementById('firstSection').appendChild(this.newSection);
  }
}
